.default, .negative {
	font-size: 16px;
}
.default span, .negative span {
	line-height: 1.8em;
	font-size: 1.5em;
	font-weight: 500;
	color: var(--color02);
}
.negative span {
	color: var(--color01);
}
.link {
	display: inline-block;
	height: 2em;
	overflow: hidden;
	white-space: nowrap;
}
.icon {
	fill: var(--color03);
	width: 1.5em;
	height: 1.5em;
	position: relative;
	bottom: 0.2em;
	transform: rotate(24deg);
}
